import { Button } from '@mui/material';
import { Stack } from '@mui/system';
import React from 'react';

export function Disclaimer(props: {setAccomplished: React.Dispatch<boolean>}) {
  return (
    <Stack sx={{
      display: 'flex',
      height: '',
      width: '100%',
      padding: "40px",
      margin: "40px",
      backgroundColor: "#fff",
      borderRadius: "30px",
    }}>
      <Stack sx={{
        fontSize: 16,
        marginBottom: "25px"
      }}>
        Az online forduln a csapatoknak nllan kell dolgozniuk, kls segtsget nem fogadhatnak el, msokkal nem kommuniklhatnak egszen a versenyzsi idszak vgig.
      </Stack>

      <Stack sx={{
        fontSize: 36,
        marginBottom: "10px",
        fontStyle: "italic",
      }}>
        Tovbbjuts
      </Stack>
      
      <Stack sx={{
        fontSize: 16,
        marginBottom: "150px",
      }}>
        Azok a csapatok, amelyek az online fordul sorn a megszerezhet 52 pontbl legalbb 25 pontot elrnek, tovbbjutnak a helyi fordulba. (Fenntartjuk a jogot, hogy a ponthatrt esetleg cskkentsk, nvelni biztosan nem fogjuk.) Az online forduln szerzett pontszm nem szmt bele a tovbbi eredmnyekbe.
      </Stack>

      <Button sx={{
        width: '350px',
        height: '60px',
        fontSize: '26px',
        alignSelf: 'center',
        textTransform: 'none',
      }} variant='contained' color='primary' onClick={()=>{props.setAccomplished(true)}}>
        Tovbb a versenyhez
      </Button>
    </Stack>
  )
}